.login-page, .create-tasks-page{
    height: 100%;
    width: 99%;
}

.title{
    font-size : 2rem;
    padding-top : 10rem;
    padding-left : 2rem;
    padding-bottom: 2rem;
    color: #007bff;
    text-align: center;
}

.login-page input {
    padding:10px;
    border: 2px solid #eee;
    box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
    border-radius:10px;
    margin:10px 0 !important;
}

.labelClass{
    padding-top : 20px;
}

.login-btn{
    margin : 2rem 0 !important;
}