.tasks-page{
    height: 100%;
    width: 99%;
}

.records{
    color: #007bff;
    font-size: 2rem;
    margin: 1rem 1rem;
}

.records-label{
    padding-left: 1rem;
}

.record-button{
    margin-left: 99px;
}

.create-task-label{
    color: #007bff;
    font-size: 2rem;
    margin: 1rem 1rem;
    padding-left: 1rem;
}

.save-button{
    margin: 1rem 1rem;
}

.create-task-table, .create-task-table tr{
    margin-top : 10px;
}

.create-task-input input {
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 4px 0px;
    width: 300px;
}

.create-task-input label {
    padding: 5px;
    margin: 4px 0px;
}

.values-table table{
    border : 1px solid #007bff;
    text-align: center;
}

.values-table input, .values-table textarea{
    margin-left : 10px;
}

.add-button{
    margin-top: 10px;
    margin-left: 500px;
    margin-bottom: 100px;
}

.add-area{
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: 100px;
}